<template>
  <div class="chart-box">
    <van-button type="primary" block @click="changeOption"></van-button>
    <div ref="echarts" class="echarts"></div>
  </div>
</template>

<script>
//  echarts  echarts 
import * as echarts from 'echarts/core';
//  Chart
import { BarChart, LineChart, PieChart  } from 'echarts/charts';
//  Component
import {
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
  LegendComponent
} from 'echarts/components';
// 
import { LabelLayout, UniversalTransition } from 'echarts/features';
//  Canvas  CanvasRenderer  SVGRenderer 
import { CanvasRenderer } from 'echarts/renderers';

// 
echarts.use([
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
  BarChart,
  LabelLayout,
  UniversalTransition,
  CanvasRenderer,
  LineChart,
  PieChart,
  LegendComponent,
]);
export default {
  name: 'Chart',
  data() {
    return {
      options: [
        {
        tooltip: {},
        xAxis: {
          data: ['', '', '', '', '', '']
        },
        yAxis: {},
        series: [
          {
            name: '',
            type: 'bar',
            data: [5, 20, 36, 10, 10, 20]
          }
        ]
      },
        {
        xAxis: {
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            data: [150, 230, 224, 218, 135, 147, 260],
            type: 'line'
          }
        ]
      },
        {
          tooltip: {
            trigger: 'item'
          },
          legend: {
            top: '5%',
            left: 'center'
          },
          series: [
            {
              name: 'Access From',
              type: 'pie',
              radius: ['40%', '70%'],
              avoidLabelOverlap: false,
              itemStyle: {
                borderRadius: 10,
                borderColor: '#fff',
                borderWidth: 2
              },
              label: {
                show: false,
                position: 'center'
              },
              emphasis: {
                label: {
                  show: true,
                  fontSize: '40',
                  fontWeight: 'bold'
                }
              },
              labelLine: {
                show: false
              },
              data: [
                { value: 1048, name: 'Search Engine' },
                { value: 735, name: 'Direct' },
                { value: 580, name: 'Email' },
                { value: 484, name: 'Union Ads' },
                { value: 300, name: 'Video Ads' }
              ]
            }
          ]
        },
      ],
      myChart: null,
      currentIndex: 0,
    }
  },
  methods: {
    initEcharts() {
      this.myChart.setOption(this.options[this.currentIndex])
    },
    changeOption() {
      this.currentIndex = ++this.currentIndex % this.options.length
      this.initEcharts()
    }
  },
  mounted() {
    this.myChart = echarts.init(this.$refs.echarts);
    this.initEcharts()
  }
}
</script>

<style lang="less" scoped>
.chart-box {
  .echarts {
    width: 100%;
    height: 500px;
  }
}
</style>
